.modalContainer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  position: relative;
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  max-width: 40%;
  text-align: center;
  font-size: 18px;
  font-weight: 600;
  color: #205f89;
  min-height: 75px;
  display: flex;
  align-items: center;
}
.modal .modal__closeIcon {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}
.modal .modal__closeIcon:hover {
  color: red;
}
.modal__msg {
  display: block;
}/*# sourceMappingURL=modal.module.css.map */